* {
  box-sizing: border-box;
  font-family: 'Alegreya', serif;
  padding: 0%;
  margin: 0%;
}

body {
  background: #40e0d0;
  background-size: cover;
  color: #fff;
}

.logo {
  display: inline-flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  color: yellowgreen;
  font-size: 1.4rem;
  font-style: italic;
  cursor: pointer;
}

.logo img {
  display: inline-flex;
  margin: .2rem;
  width: 40px;
}

.topnav {
  width: 100%;
  background: #004953;
  padding: .4rem;
  text-align: right;
}

.topnav a {
  display: inline-block;
  align-content: center;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  padding: 10px 10px;
}

.topnav a:hover {
  text-decoration: overline;
  text-decoration-style: double;
  text-decoration-color: yellowgreen;
}

.list-container {
  display: inline-flex;

  justify-content: center;
  justify-items: center;
  align-content: center;
  align-items: center;
}

.mylist {
  background: #004953;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  text-align: center;
  padding: 10px 10px;
  max-width: 180px;
  max-height: 180px;
  min-width: 180px;
  min-height: 180px;
  color: #fff;
  border-radius: 50%;
  font-size: 1.2rem;
}

.description a {
  color: #fff;
  text-decoration: none;
}

.description a:hover {
  color: #8A0707;
}

.description a:focus {
  color: hotpink;
}

.home h1 {
  background: #004953;
  color: #fff;
  text-align: center;
  margin: 0% 8% 0% 8%;
}

.home .p {
  background: #004953;
  color: #fff;
  text-align: justify;
  margin: .5% 10% .5% 10%;
  padding: .8rem;
  font-size: 1.5rem;
  line-height: 35px;
}

/* styles for Footer... */

.footer {
  position: relative;
  width: 100%;
  bottom: 0%;
  left: 0%;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #004953;
  color: #fff;
  padding: 0px;
}

.footer p {
  bottom: 0%;
  left: 0%;
  font-size: 1.2rem;
  vertical-align: middle;
}

.footer .icons {
  background-color: #131313;
  font-size: 2rem;
  vertical-align: middle;
  margin: .8rem;
  color: yellowgreen;
}

/* end of styles for Footer... */

/* Starting styles for Start page... */

.start-images {
  width: 100%;

  object-fit: fill;
  padding: 0% 8% 5% 8%;
}

.myContent h1, h2 {
  text-align: center;
  text-decoration: underline;
  font-family: 'Arvo', serif;
  padding: .8rem;
  color: #fff;
}

.myContent p {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 2% 10% 2% 10%;
  margin: 2% 10% 2% 10%;
  text-align: justify;
  font-size: 1.4rem;
  line-height: 50px;
}

strong {
  font-family: 'Times New Roman', Times, serif;
}

/* End of start page styles */

/* starting styles for Training page */

iframe {
  width: 100%;
  height: 500px;
  outline: none;
  padding: 1rem;
}

ol li {
  list-style-type: lower-roman;
}

ul li {
  list-style: circle;
}

ul li i {
  color: #8cab45;
}

/* end of training styles */

/* start of styles for Slider page */

.slide-container {
  display: flex;
}

.slide {
  width: 100%;
  position: relative;
  left: 0%;
}

.slide img {
  display: flex;
  width: 100%;
  height: 65%;
  padding: 1rem;
  object-fit: fill;
  border-radius: 2.2rem;
}


.slide button {
  display: flex;
  outline: none;
  padding: 10px 0px;
}

.slide .left {
  position: absolute;
  left: 1.3%;
  top: 2%;
  display: grid;
  height: 61.1%;
  background: rgba(0, 0, 0, 0.5);
  align-items: center;
  border-radius: 20px;
  justify-content: center;
}

.slide .center {
  position: absolute;
  top: 30%;
  left: 25%;
  text-align: center;
}

.slide .center p {
  align-items: center;
  font-size: 2rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background: rgba(0, 0, 0, 0.5);
  padding: 1rem;
  border-radius: .8rem;
  text-align: center;
}

.slide .right {
  display: grid;
  height: 61.1%;
  position: absolute;
  right: 1.3%;
  top: 2%;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.5);
}

.slide button {
  background: transparent;
  outline: none;
  border: none;
  font-size: 1.2rem;
  color: #fff;
  padding: 1rem;
}

/* End of styles of slider page */